@charset "UTF-8";
body {
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 400;
  color: #333333;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

* {
  min-height: 0vw;
  min-height: 0;
}

.example {
  font-size: clamp(1.5rem, 0.3636363636rem + 4.8484848485vw, 4rem);
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
@media screen and (min-width: 768px) {
  a:hover {
    opacity: 0.8;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.drawer-icon {
  width: 27px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.drawer-icon__bar {
  position: relative;
  width: 27px;
  height: 1.5px;
  background: #333333;
  border-radius: 4px;
  display: block;
}
.drawer-icon__bar:nth-of-type(1) {
  top: 0;
}
.drawer-icon__bar:nth-of-type(2) {
  margin-block: 6px;
}

.header {
  height: clamp(3.75rem, 2.7285714286rem + 4.1904761905vw, 6.5rem);
}

.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 15px;
}

.header__logo {
  width: clamp(9.4375rem, 8.6017857143rem + 3.4285714286vw, 11.6875rem);
}

.header__button {
  width: clamp(8rem, 7.2571428571rem + 3.0476190476vw, 10rem);
  height: clamp(2.5rem, 2.1285714286rem + 1.5238095238vw, 3.5rem);
}

.header__button-arrow {
  margin-left: 7px;
}

.section-inner {
  padding-inline: 15px;
}

.section-title {
  font-family: "Noto Serif JP", sans-serif;
  font-weight: 400;
  font-size: clamp(1.5rem, 1.1285714286rem + 1.5238095238vw, 2.5rem);
}

.section-title--deco {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  white-space: nowrap;
  position: relative;
  max-width: clamp(25rem, 11.3732142857rem + 55.9047619048vw, 61.6875rem);
  width: 100%;
  margin: 0 auto;
}
.section-title--deco::before, .section-title--deco::after {
  position: absolute;
  content: "";
  width: clamp(1.5625rem, 1.3767857143rem + 0.7619047619vw, 2.0625rem);
  height: clamp(2.5rem, 2.1982142857rem + 1.2380952381vw, 3.3125rem);
  background-size: contain;
}
.section-title--deco::before {
  background-image: url(../images/heading-left.png);
  left: 8px;
}
.section-title--deco::after {
  background-image: url(../images/heading-right.png);
  right: 8px;
}

.button {
  background: #FF8B71;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  border-radius: clamp(2rem, 1.8142857143rem + 0.7619047619vw, 2.5rem);
  font-weight: 700;
}

.talk {
  display: flex;
  gap: clamp(1.1875rem, 1.0714285714rem + 0.4761904762vw, 1.5rem);
}
.talk + .talk {
  margin-top: 16px;
}

.talk--reverse {
  flex-direction: row-reverse;
  margin-left: 20px;
}
.talk--reverse .talk__balloon::before {
  left: initial;
  right: -16px;
  transform: scale(-1, 1);
}

.talk__icon {
  width: clamp(3.125rem, 2.1964285714rem + 3.8095238095vw, 5.625rem);
  flex: 0 0 50px;
}

.talk__icon--hidden {
  opacity: 0;
}

.talk__balloon {
  background: #fff;
  box-shadow: 0px 0px 4px 0px rgba(0, 29, 104, 0.25);
  border-radius: 24px;
  padding: 16px;
  position: relative;
}
.talk__balloon::before {
  position: absolute;
  content: "";
  width: 17px;
  height: 32px;
  top: 19px;
  left: -16px;
  background-image: url(../images/balloon-triangle.svg);
  background-repeat: no-repeat;
}

.talk__text {
  font-size: clamp(0.9375rem, 0.8214285714rem + 0.4761904762vw, 1.25rem);
  line-height: 170%;
  letter-spacing: clamp(-0.009375rem, -0.0267857143rem + 0.0714285714vw, 0.0375rem);
}

.talk__text-strong {
  font-weight: 700;
  background-color: #C6ECEC;
}

.campaign {
  padding-top: clamp(1.5rem, 1.1285714286rem + 1.5238095238vw, 2.5rem);
  padding-bottom: clamp(1.5rem, 1.0589285714rem + 1.8095238095vw, 2.6875rem);
  padding-inline: 15px;
  background: url(../images/campaign-bg-sp.png);
  background-position: center;
  background-size: cover;
  background-repeat: repeat;
}

.cta {
  background: #C6ECEC;
  position: relative;
}

.cta__top {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  transform: translateY(-100%) translateY(2px) scale(1, 1);
  transform-origin: top;
  width: 100%;
}

.cta__inner {
  padding-top: 53.5px;
  padding-bottom: 120px;
}

.cta__content {
  position: relative;
}

.cta__title {
  font-family: "Noto Sans JP", sans-serif;
  line-height: 85%;
  letter-spacing: -0.72px;
}

.cta__title--strong {
  font-weight: 700;
}

.cta__button-boxes {
  margin-top: 64.5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 48px;
}

.cta__button-text {
  font-weight: 700;
  display: flex;
  justify-content: center;
  gap: 8px;
}
.cta__button-text::before, .cta__button-text::after {
  content: "";
  display: block;
  width: 2px;
  height: 22px;
  background-color: #6C6E6F;
}
.cta__button-text::before {
  transform: rotate(-20deg);
}
.cta__button-text::after {
  transform: rotate(20deg);
}

.cta__button {
  margin-top: clamp(0.5rem, 0.4071428571rem + 0.380952381vw, 0.75rem);
  font-size: clamp(1.375rem, 1.1892857143rem + 0.7619047619vw, 1.875rem);
  padding: 20px 10px;
  width: clamp(18.75rem, 16.4285714286rem + 9.5238095238vw, 25rem);
  height: clamp(3.5rem, 2.7571428571rem + 3.0476190476vw, 5.5rem);
}

.cta__button--deco {
  font-size: clamp(1.25rem, 1.0642857143rem + 0.7619047619vw, 1.75rem);
}

.cta__button-arrow {
  transform: translateX(10px);
}

.cta__image {
  position: absolute;
  width: 77px;
  height: 136px;
  bottom: -40px;
  left: 50%;
  transform: translateX(-50%);
}

.example {
  background: #F4FBFD;
}

.example__inner {
  padding-top: clamp(3.5rem, 2.9428571429rem + 2.2857142857vw, 5rem);
  padding-bottom: clamp(2.5rem, 1.5017857143rem + 4.0952380952vw, 5.1875rem);
}

.example__content {
  background: #fff;
  box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.25);
  border-radius: 24px;
  padding: 40px 11.5px;
}

.example__heading {
  text-align: center;
}

.example__lead {
  font-family: "Noto Serif JP", sans-serif;
  font-size: clamp(0.9375rem, 0.5892857143rem + 1.4285714286vw, 1.875rem);
  margin-top: 1px;
}

.example__talk {
  margin-top: 24.5px;
}
.example__talk .talk--reverse {
  margin-left: 0;
}

.point__inner {
  padding-top: clamp(3.5rem, 2.9428571429rem + 2.2857142857vw, 5rem);
  padding-bottom: 40px;
}

.point__title {
  text-align: center;
}

.point__items {
  margin-top: clamp(1.5rem, 1.1285714286rem + 1.5238095238vw, 2.5rem);
}

.point__item + .point__item {
  margin-top: 40px;
}

.point__item-number {
  font-size: 64px;
  font-family: "Lato", sans-serif;
  font-weight: 400;
  color: #82BFED;
  position: relative;
  margin-left: 19px;
}
.point__item-number::after {
  position: absolute;
  content: "point";
  top: 30px;
  left: -23px;
  color: #333333;
  background-color: #fff;
  font-size: 16px;
  text-transform: uppercase;
}

.point__item-image {
  border-radius: 2px;
}

.point__item-content {
  margin-top: 24px;
}

.point__item-title {
  font-size: clamp(1.25rem, 1.1571428571rem + 0.380952381vw, 1.5rem);
  font-weight: 700;
  color: #2273AE;
}

.point__item-text {
  margin-top: clamp(1rem, 0.8142857143rem + 0.7619047619vw, 1.5rem);
  line-height: 170%;
  letter-spacing: 0.48px;
}

.point__item-text--note {
  color: #6C6E6F;
}

.problem__inner {
  background: url(../images/problem-bg-sp.png);
  background-position: center;
  background-size: cover;
  background-repeat: repeat;
}

.problem__head {
  padding-top: clamp(2.5rem, 1.5714285714rem + 3.8095238095vw, 5rem);
  text-align: center;
}

.problem__body {
  margin: 0 auto;
  margin-top: clamp(1.25rem, 1.1339285714rem + 0.4761904762vw, 1.5625rem);
  display: flex;
  max-width: clamp(26.25rem, 15.3625rem + 44.6666666667vw, 55.5625rem);
  width: 100%;
  padding-right: 15px;
}

.problem__image {
  position: absolute;
  width: clamp(10.1875rem, 5.4285714286rem + 19.5238095238vw, 23rem);
  height: clamp(15.0625rem, 12.1142857143rem + 12.0952380952vw, 23rem);
  z-index: -1;
}

.problem__items {
  position: relative;
  display: flex;
  flex-direction: column;
  flex: 1;
  align-items: flex-end;
  margin-top: 30px;
  padding-bottom: clamp(1.9375rem, 1.7053571429rem + 0.9523809524vw, 2.5625rem);
}

.problem__item {
  width: clamp(15.5625rem, 6.4625rem + 37.3333333333vw, 40.0625rem);
  border: 0.395px solid #6C6E6F;
  border-radius: clamp(0.5rem, 0.3142857143rem + 0.7619047619vw, 1rem);
  display: flex;
  align-items: baseline;
  padding: 9.474px 9.474px 9.474px 8px;
  background: #fff;
}
.problem__item + .problem__item {
  margin-top: clamp(1.1875rem, 0.9553571429rem + 0.9523809524vw, 1.8125rem);
}

.problem__lead {
  font-size: clamp(1rem, 0.8142857143rem + 0.7619047619vw, 1.5rem);
  font-weight: 400;
  line-height: 130%;
  letter-spacing: -0.48px;
  margin-left: clamp(0.395rem, 0.1702857143rem + 0.9219047619vw, 1rem);
}

.problem__item-image {
  width: clamp(0.625rem, 0.3rem + 1.3333333333vw, 1.5rem);
}

.problem__foot {
  background-color: #82BFED;
  padding-block: 17px;
  text-align: center;
  position: relative;
  width: 100%;
}
.problem__foot::after {
  position: absolute;
  content: "";
  width: 0;
  height: 0;
  left: 50%;
  transform: translateX(-50%) translateY(17px);
  border-left: 24px solid transparent;
  border-right: 24px solid transparent;
  border-top: 24px solid #82BFED;
}

.problem__text {
  font-size: clamp(1.0625rem, 0.7142857143rem + 1.4285714286vw, 2rem);
  letter-spacing: -0.51px;
}

.recommend__inner {
  margin-top: clamp(2.5rem, 1.5714285714rem + 3.8095238095vw, 5rem);
  margin-bottom: clamp(2.5rem, 1.85rem + 2.6666666667vw, 4.25rem);
}

.recommend__items {
  margin-top: clamp(2.0625rem, 0.9714285714rem + 4.4761904762vw, 5rem);
}

.recommend__item {
  position: relative;
}
.recommend__item + .recommend__item {
  margin-top: 56px;
}

.recommend__item-icon {
  position: absolute;
  background-color: #fff;
  width: 64px;
  height: 64px;
  border-radius: 64px;
  filter: drop-shadow(0px 1px 1px rgba(0, 0, 0, 0.25));
  display: grid;
  place-content: center;
  top: 7px;
  left: 7px;
}

.recommend__item-number {
  font-family: "Lato", sans-serif;
  font-size: 30px;
  font-weight: 500;
  background: linear-gradient(177deg, #50BFCB -29.45%, rgba(80, 191, 203, 0.42) 127.88%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.recommend__item-image img {
  border-radius: 14.8px;
  box-shadow: 0px 3.7px 3.7px 0px rgba(0, 0, 0, 0.25);
}

.recommend__item-title {
  margin-top: 24px;
  font-size: clamp(1.25rem, 1.1571428571rem + 0.380952381vw, 1.5rem);
  text-align: center;
}

.recommend__item-text {
  margin-top: 8px;
  line-height: 150%;
  letter-spacing: 0.48px;
}

.room__inner {
  margin-top: 90px;
}

.room__text {
  margin-top: clamp(2.0625rem, 1.9rem + 0.6666666667vw, 2.5rem);
  text-align: center;
  font-size: clamp(0.875rem, 0.7821428571rem + 0.380952381vw, 1.125rem);
  line-height: 120%;
}

.solution {
