}

.solution__title-image {
  width: clamp(9.4375rem, 8.6017857143rem + 3.4285714286vw, 11.6875rem);
  padding-right: 8px;
}

.solution__lead {
  line-height: 170%;
  letter-spacing: 0.48px;
  margin-top: 12px;
  text-align: center;
}

.solution__items {
  margin-top: clamp(1.875rem, 0.7142857143rem + 4.7619047619vw, 5rem);
  display: flex;
  gap: clamp(0.1875rem, -1.0660714286rem + 5.1428571429vw, 3.5625rem);
}

.solution__content {
  margin-top: clamp(3.5rem, 2.9428571429rem + 2.2857142857vw, 5rem);
}

.solution__content-title {
  font-family: "Noto Serif JP", sans-serif;
  font-weight: 400;
  font-size: clamp(1.5rem, 1.3142857143rem + 0.7619047619vw, 2rem);
  text-align: center;
}

.solution__content-talk {
  margin-top: clamp(2.625rem, 2.5785714286rem + 0.1904761905vw, 2.75rem);
}

.use {
  background: #F4FBFD;
}

.use__inner {
  padding-block: clamp(3.5rem, 2.9428571429rem + 2.2857142857vw, 5rem);
}

.use__content {
  background: #fff;
  box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.25);
  border-radius: 24px;
  padding: 40px 15px;
}

.use__text {
  margin-top: clamp(1.5625rem, 1.2142857143rem + 1.4285714286vw, 2.5rem);
  font-size: clamp(0.9375rem, 0.8678571429rem + 0.2857142857vw, 1.125rem);
  line-height: 150%;
}